table{
  border-radius:4px;
  font-family: sans-serif, Calibri, Gill Sans, Gill Sans MT,  Trebuchet MS;
}
table, th, td {
  border-collapse: collapse;
  text-align:center;
  font-size: 12px;
}

td{
  min-width:50px;
}

table th{
font-weight: 600;
height:38px;
background-color: #F4F4F4;
border-top:2px solid #dcdada;
}

th ,td{
  width:fit-content;
  min-height:20px;
  padding: 10px;
}

.input-field{
width:85%;
height:70%;
border:none;
text-align: center;
outline:none;
border-radius:7px;
}

.input-field:focus,

.input-field:active{
border:1px solid #dcdada;
background-color: #F4F4F4;
}

td.static{
  position:sticky;
  left:0;
  background-color:white;
  z-index:1
}

th.static{
  background-color: #F4F4F4;
  position:sticky;
  z-index:1
}

 td:nth-child(1) ,th:nth-child(1){
  left:0;
} 

td:nth-child(2) ,th:nth-child(2){
  left:70px;
}

td:nth-child(3) ,th:nth-child(3){
  left:140px;
  border-right:1px solid #dcdada;
}


